div#headerContainer {
  direction: rtl;
}
img#logo {
  border-style: solid;
  border-width: 0px;
  border-color: black;
  float: right;
  width: 90px;
  height: 90px;
  margin-top: 5px;
  margin-right: 15px;
}

div#NavBarContainer {
  /* font-family: Arial, Helvetica, sans-serif; */
  padding-top: 40px;
  background-color: #eee3;
  padding-right: 0px;
}

div#bodyContainer {
  width: 60%;
  min-height: 400px;

  border-style: solid;
  border-width: 0px;
  border-color: #9876;
  margin-left: 20%;
  align-content: center;
  justify-content: table;

  /* font-family: Verdana, Geneva, Tahoma, sans-serif; */

  /* display: flex; */
  align-items: center;
  direction: rtl;
  /* background-image: url("./imgs/logo.jpeg");
  opacity: 0.05; */
  background-color: #ffffff; /* Used if the image is unavailable */
  /* height: 500px; You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;

  

}
div#imageSliderContainer{
width: 100%;
/* height: 400px; */
}
img#bbg {
  background-image: url("./imgs/logo.jpeg");
  border-style: solid;
  border-width: 0px;
  border-color: black;
  float: right;
  width: 90px;
  height: 90px;
  margin-top: 5px;
  margin-right: 15px;
}
div#raceTitleBoxContainer{
  width:100%;
  float:left;
  background:#ccc;
  align-items: center;
  display: flex;
}

div#raceTitleBox{
  width: 90%;
  height: 100px;
  margin-right: auto;
  margin-left: auto;
  /* margin-top: 10px; */
  border-style: solid;
  border-width: 1px;
  border-color: #444;
  text-align: justify;
  color: #567
}
div#contactusContent{
  width: 80%;
  margin-right: 10%;
  display: flex;
}

div#contactusBox{
  width: 100%;
  height: 200px;
  margin-top: 100px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 17px;
  border-style: solid;
  border-width: 0px;
  border-color: #4441;
  text-align: justify;
  font-size: 15px;
}
div#displayNewsInfo{
  width: 90%;
  direction: rtl;

  border-style: solid;
  border-width: 0px;
  border-color: #444;
  
  margin: 5%;
  /* margin-left: 10%; */
  text-align: right;
  text-align: justify;
}
div#allNewsLinksContainer p{
cursor: pointer;
}
div#allNewsLinksContainer{
  width: 60%;
  margin-top: 40px;
  text-align: right;
  color: #656625;
  font-size: 20px;
}
div#reportContainer{
  width:100%;
  float: right;;
  background:#fff;
  align-items: center;
  /* display: flex; */
}
#printMe {
  width: 100%;
  border-style: solid;
  border-width: 0px;
  border-color: #4441;

  text-align: center;
  font-size: 15px;
  /* display: flex; */
  margin-left: auto;
  margin-right: auto;
  float: right;
}
div#printMeLine{
  margin-top: 50px;
  width: 100%;
  display: flex;
  direction: rtl;
  font-size: 25px;
  /* margin-left: 2%;
  margin-right: 2%; */
  
  margin-left: auto;
  margin-right: auto;
}
table#printMeTable{
  width: 100%;
  direction: rtl;
  margin-left: auto;
  margin-right: auto;
  
  border-bottom-width: 5px;
  border-bottom-color: #822;
  border-bottom-style: solid;
}
table#printMeTable p{
  font-size:50px;
}
table#printMeTable td{
  width: 33%;
  border-style: solid;
  border-width: 0px;
  border-color: #4441;
  text-align: center;
}

div#printingControl{
  width: 90%;
  margin-right: 5%;
  float: right;
  text-align: center;

}
div#reportBox{
  width: 90%;
  height: 100px;
  margin-right: auto;
  margin-left: auto;
  /* margin-top: 10px; */
  border-style: solid;
  border-width: 1px;
  border-color: #444;
  text-align: justify;
  color: #567
}
table#horseInfo {
width: 90%;
text-align: center;
margin-right: 5%;
}
table#horseInfo td {
  border-style: solid;
  border-width: 1px;
  border-color: #444;
  }

div#footerContainer {
  
  border-style: solid;
  border-width: 0px;
  border-color:#9876;
  width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  direction: rtl;
  background-color: #333;
}
div#footerContent{
  width: 80%;
  margin-right: 10%;
  display: flex;
}
div#footerBox{
  width: 30%;
  height: 200px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 17px;
  border-style: solid;
  border-width: 1px;
  border-color: #4441;
  text-align: justify;
  color: #ffffff55;
  font-size: 15px;
}
div#copyRight{
  width: 100%;
  height: 20px;
  background-color: #7be2af
}
div#line{
  width: 100%;
  height: 10px;
  background-color: #222;
  
}



.droid-arabic-kufi {
  font-family: 'Droid Arabic Kufi', serif;
